import React from 'react';
import ReactModal from 'react-modal';
import "./PopUp.css";

class Popup extends React.Component {
  constructor () {
    super();

    this.handleClickCartPopup = this.handleClickCartPopup.bind(this);
    this.state = {showModal: false };
  
    this.handleOpenModal = this.handleOpenModal.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
  }


  handleClickCartPopup(product) {
    this.props.handleRemoveFromCart(product);
  }
  
  handleOpenModal () {
    this.setState({ showModal: true });
  }
  
  handleCloseModal () {
    this.setState({ showModal: false });
  }

  render () {
    return (
      <div id="popup">
        <button className="popupButton" onClick={this.handleOpenModal}>FAQ
        </button>

        <ReactModal id="popup"
           dialogClassName="test"
           isOpen={this.state.showModal}
           contentLabel="onRequestClose Example"
           onRequestClose={this.handleCloseModal}
           style={{
            overlay: {
              backgroundColor: 'linear-gradient(90deg, #FC466B 0%, #3F5EFB 100%)'
              
            },
            content: {
              color: 'black',
              backgroundColor: 'papayawhip',        
            }
            
          }}> 
          <p className="headlinePopup">Vanliga frgor</p>
         <p> 
          <strong>Varfr ska jag logga in p Spotify?</strong>
          <p className="answersPopup">Om du loggar in, och har Spotify Premium-konto, s kommer du att kunna spela hela lngden p ltarna, annars s kommer du att f en frkortad version p ltarna som spelas i 30 sekunder.</p>
          <strong>Hur loggar jag ut frn Spotify?</strong>
          <p className="answersPopup">G in p ditt konto p Spotifys hemsida och klicka p "Logga ut verallt".</p>
          <strong>Hur skapar jag ett nytt spel?</strong>
          <p className="answersPopup">Klicka p "Generera nytt spel" och vlj drefter kriterier, sen r det bara att bjuda in kompisarna med hjlp av pinkoden.</p>
          <strong>Vilken webblsare r bst att anvnda?</strong>
          <p className="answersPopup">TuneBash fungerar bst med webblsaren Google Chrome.</p>
          <strong>Har du ovanliga frgor?</strong>
          <p className="answersPopup" id="bottomFAQ">Kontakta oss p blablabla.</p>
           </p>
         <div className="closeDiv">
         <button className="closeButton" onClick={this.handleCloseModal}>Stng</button>
         </div>
        </ReactModal>
      </div>
    );
  }
}


export default Popup;


